* {
  box-sizing: border-box;
  margin: 0;
  padding: 0;
}

body {
  width: 100vw;
  height: 100vh;
  background: #312e2b;
  justify-content: center;
  align-items: center;
  font-family: Arial, Helvetica, sans-serif;
  display: flex;
}

h1.title {
  color: #fff;
  text-align: center;
  margin-bottom: 3rem;
}

.chessboard {
  width: 500px;
  height: 500px;
  border-radius: 25px;
  grid-template-columns: 1fr 1fr 1fr 1fr 1fr 1fr 1fr 1fr;
  display: grid;
}

.chessboard > .chessboard-square {
  width: 62.5px;
  height: 62.5px;
  justify-content: center;
  align-items: center;
  display: flex;
}

.chessboard > .chessboard-square[role="white"] {
  background: #fff;
}

.chessboard > .chessboard-square[role="black"] {
  background: #000;
}

.chessboard > .chessboard-square:nth-child(1) {
  border-top-left-radius: 25px;
}

.chessboard > .chessboard-square:nth-child(8) {
  border-top-right-radius: 25px;
}

.chessboard > .chessboard-square:nth-child(57) {
  border-bottom-left-radius: 25px;
}

.chessboard > .chessboard-square:nth-child(64) {
  border-bottom-right-radius: 25px;
}

.chessboard > .chessboard-square:hover {
  cursor: pointer;
}

.chessboard > .chessboard-square.possible-move {
  background: #bada55;
}

@media (max-width: 40rem) {
  .chessboard {
    width: 95vw;
    height: 95vw;
  }

  .chessboard > .chessboard-square {
    width: 11.875vw;
    height: 11.875vw;
  }
}

img.chessboard-piece {
  width: 50px;
  height: auto;
}

@media (max-width: 40rem) {
  img.chessboard-piece {
    width: 11.875vw;
  }
}

